//
export default {
  data () {
    return {
      documentHeight: '',
    }
  },
  watch: {

  },
  created() {
    this.getHight()
    //
    window.addEventListener('resize', this.getHight);
  },
  destroyed() {
    window.removeEventListener('resize', this.getHight)
  },
  methods: {
    //
    getHight(){
      this.documentHeight = window.innerHeight - 230 + 'px'
    }
  }
}