<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row :gutter="20">
        <el-col :span="6">
          <el-input
            size="small"
            clearable
            placeholder=""
            v-model="queryInfo.query"
            @clear="getList"
          >
            <el-button
              slot="append"
              @click="getList"
              icon="el-icon-search"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button type="primary" size="small" @click="addUserDialog = true"
            ></el-button
          >
        </el-col>
      </el-row>
      <el-row>
        <el-table :data="tableData" border stripe>
          <el-table-column type="index" align="center" size="mini" width="50">
          </el-table-column>
          <el-table-column
            align="center"
            v-for="(item, index) in column"
            :key="index"
            size="mini"
            :prop="item.name"
            :min-width="item.minWidth"
            :label="item.label"
          >
          </el-table-column>
          <el-table-column align="center" label="" min-width="160px">
            <template slot-scope="scope">
              <el-switch
                size="mini"
                v-model="scope.row.mg_state"
                @change="userStateChange(scope.row)"
              >
              </el-switch>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            label=""
            width="220px"
            fixed="right"
          >
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                @click="ShowEditDialog(scope.row.id)"
                size="mini"
              >
              </el-button>
              <el-button
                type="danger"
                icon="el-icon-delete"
                @click="removeUser(scope.row.id)"
                size="mini"
              >
              </el-button>
              <el-tooltip
                effect="dark"
                content=""
                placement="top"
                :enterable="false"
              >
                <el-button type="warning" icon="el-icon-setting" size="mini" 
                @click="showRolesDialog(scope.row)">
                </el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </el-row>
      <el-row>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="queryInfo.pagenum"
          :page-sizes="[2, 5, 10, 15]"
          :page-size="queryInfo.pagesize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </el-row>
    </el-card>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addUserDialog"
      width="40%"
      @close="addDialogClosed"
    >
      <el-form
        :model="addUserForm"
        :rules="addUserFormRules"
        ref="addUserFormRef"
        label-width="70px"
      >
        <el-form-item label="" prop="username">
          <el-input size="small" v-model="addUserForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input size="small" v-model="addUserForm.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input size="small" v-model="addUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input size="small" v-model="addUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="addUserDialog = false" size="small"> </el-button>
        <el-button type="primary" @click="addUser" size="small"
          > </el-button
        >
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editUserDialog"
      width="40%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editUserForm"
        :rules="editUserFormRules"
        ref="editUserFormRef"
        label-width="70px"
      >
        <el-form-item label="">
          <el-input
            size="small"
            v-model="editUserForm.username"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input size="small" v-model="editUserForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input size="small" v-model="editUserForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button @click="editUserDialog = false" size="small"> </el-button>
        <el-button type="primary" @click="editUser" size="small"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRolesDialog"
      width="40%"
      @close="setRolDialogClosed"
    >
      <div>
        <p>{{userInfo.username}}</p>
        <p>{{userInfo.role_name}}</p>
        <p>
          <el-select v-model="selectedRoleId" placeholder="" size="small">
            <el-option
              v-for="item in rolesList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id">
            </el-option>
          </el-select>          
        </p>
      </div>
      <div slot="footer">
        <el-button @click="setRolesDialog = false" size="small"> </el-button>
        <el-button type="primary" @click="setRoles" size="small"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { type } from 'os'
import table from '@/mixins/tables'
export default {
  data() {
    //
    var checkEmail = (rule, val, cb) => {
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(val)) {
        return cb()
      } else {
        cb(new Error(''))
      }
    }
    //
    var checkMobile = (rule, val, cb) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(val)) {
        return cb()
      } else {
        cb(new Error(''))
      }
    }

    return {
      tableData: [],
      column: [
        { name: 'username', label: '', minWidth: '160px' },
        { name: 'email', label: '', minWidth: '160px' },
        { name: 'mobile', label: '', minWidth: '160px' },
        { name: 'role_name', label: '', minWidth: '160px' }
      ],
      addUserDialog: false,
      addUserForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      addUserFormRules: {
        username: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 15,
            message: ' 6  15 ',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      editUserDialog: false,
      editUserForm: {},
      editUserFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      setRolesDialog: false,
      rolesList: [],
      userInfo: {},
      selectedRoleId: ''
    }
  },
  mixins:[table],
  created() {
    this.getList();
  },
  methods: {
    async getList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status == 200) {
        this.tableData = res.data.users
        this.total = res.data.total
      } else {
        this.$message({
          message: res.meta.msg,
          type: 'error'
        })
      }
    },
    async userStateChange(userInfo) {
      const { data: res } = await this.$http.put(
        `users/${userInfo.id}/state/${userInfo.mg_state}`
      )
      if (res.meta.status == 200) {
        this.$message({
          message: res.meta.msg,
          type: 'success'
        })
      } else {
        userInfo.mg_state = !userInfo.mg_state
        this.$message({
          message: res.meta.msg,
          type: 'error'
        })
      }
    },
    addDialogClosed() {
      this.$refs.addUserFormRef.resetFields()
    },
    //
    addUser() {
      this.$refs.addUserFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post('users', this.addUserForm)
          if (res.meta.status == 201) {
            this.$message({
              message: res.meta.msg,
              type: 'success'
            })
            this.addUserDialog = false
            this.getList()
          } else {
            this.$message({
              message: res.meta.msg,
              type: 'error'
            })
          }
        }
      })
    },
    //
    async ShowEditDialog(id) {
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status == 200) {
        this.editUserForm = res.data
        this.editUserDialog = true
      } else {
        this.$message({
          message: res.meta.msg,
          type: 'error'
        })
      }
    },

    editDialogClosed() {
      this.$refs.editUserFormRef.resetFields()
    },
    //
    editUser() {
      this.$refs.editUserFormRef.validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.put(
            'users/' + this.editUserForm.id,
            {
              email: this.editUserForm.email,
              mobile: this.editUserForm.mobile
            }
          )
          if (res.meta.status == 200) {
            this.$message({
              message: res.meta.msg,
              type: 'success'
            })
            this.editUserDialog = false
            this.getList()
          } else {
            this.$message({
              message: res.meta.msg,
              type: 'error'
            })
          }
        }
      })
    },
    //
    async removeUser(id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch(err => err)
      //  confirm
      //  cancel
      if (confirmResult == 'confirm') {
        const { data: res } = await this.$http.delete('users/' + id)
        if (res.meta.status == 200) {
          this.$message({
            message: res.meta.msg,
            type: 'success'
          })
          this.getList()
        } else {
          this.$message({
            message: res.meta.msg,
            type: 'error'
          })
        }
      } else {
        this.$message({
          message: '',
          type: 'info'
        })
      }
    },
 
    async showRolesDialog(userInfo) {
      this.userInfo = userInfo
      const {data: res}= await this.$http.get('roles')
      if (res.meta.status == 200) {
        this.rolesList = res.data
        this.setRolesDialog = true
      } else {
        this.$message({
          msg: res.meta.msg,
          type: 'error'
        })
      }     
    },
    setRolDialogClosed() {
      this.selectedRoleId = ''
      this.userInfo = {}
    },
    //
    async setRoles() {
      if (this.selectedRoleId) {
        const {data: res} = await this.$http.put(`users/${this.userInfo.id}/role`, {
          rid: this.selectedRoleId
        })
        if (res.meta.status == 200) {
          this.$message({
            message: res.meta.msg,
            type: 'success'
          }) 
          this.getList()
          this.setRolesDialog = false
        } else {
          this.$message({
            message: res.meta.msg,
            type: 'error'
          })             
        }
      } else {
        this.$message({
          message: '',
          type: 'error'
        })
      }  
    }
  }
}
</script>

<style lang="less" scoped>

</style>